import React from 'react';

function DataTable() {
    
    return (
        <div className="table-responsive">
            <table className="table">
                <thead className="thead-light">
                    <tr>
                        <th scope="col" className="text-midnight">No.</th>
                        <th scope="col" className="text-midnight">Alarm Level</th>
                        <th scope="col" className="text-midnight">Device Name</th>
                        <th scope="col" className="text-midnight">Alarm Information</th>
                        <th scope="col" className="text-midnight">Trigger Value</th>
                        <th scope="col" className="text-midnight">Date/Time</th>
                        <th scope="col" className="text-midnight">Acknowledgement</th>
                    </tr>
                </thead>

                <tbody>
                    <tr>
                        <th scope="row">1</th>
                        <td className="text-dark">Critical</td>
                        <td className="text-dark">PDU1</td>
                        <td className="text-dark">Emergency Fan Open Alarm</td>
                        <td className="text-dark">-</td>
                        <td className="text-dark">20-06-2020 23:21:26</td>
                        <td className="text-dark">
                            <a className="d-block btn btn-sm btn-warning font-weight-bold" href="#" >
                                Acknowledge</a></td>
                    </tr>
                    <tr>
                        <th scope="row">2</th>
                        <td className="text-dark">Moderate</td>
                        <td className="text-dark">Air Conditioner</td>
                        <td className="text-dark">AC Power-off Alarm</td>
                        <td className="text-dark">OFF</td>
                        <td className="text-dark">20-06-2020 23:21:22</td>
                        <td className="text-dark">
                            <a className="d-block btn btn-sm btn-warning font-weight-bold" href="#" >
                                Acknowledge</a></td>
                    </tr>
                </tbody>
            </table>
        </div>
    )
}

export default DataTable;